@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  font-family: sans-serif;
}

nav {
  z-index: 10;
}

.flex {
  display: flex;
}

.h-screen {
  height: 100vh;
}



.pane {
  overflow-y: auto;
}

.infographic-step {
  display: flex;
  align-items: center;
}

.infographic-step::before {
  content: '';
  width: 1px;
  height: 100%;
  background-color: gray;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
